import React, { Component } from "react";
import ReactDOM from "react-dom";
import Square from "./Square";
import "./index.css";

class Game extends Component {
  constructor(props) {
    super(props);
    this.handleKeyDown = this.handleKeyDown.bind(this);
    this.state = {
      on: [{
      }],
      direction: 'north',
      disabled: false,

    };  
  }

  addPos(x, y) {
    if (x === -1 || x === 8 || y === -1 || x === 8) {
      return this.setState({
        disabled: true,
      });
    }
    this.setState(prevState => ({
      on: [...prevState.on, { posX: x, posY: y }],
    }));
  }

  addPosFromDirection() {
    const x = this.state.on;
    switch (this.state.direction) {
      case 'north':
        this.addPos(x[x.length - 1].posX, x[x.length - 1].posY - 1);
        break;
      case 'east':
        this.addPos(x[x.length - 1].posX + 1, x[x.length - 1].posY);
        break;
      case 'south':
        this.addPos(x[x.length - 1].posX, x[x.length - 1].posY + 1);
        break;
      case 'west':
        this.addPos(x[x.length - 1].posX - 1, x[x.length - 1].posY);
        break;
      default:
        return new Error("There is no direction in the state");
    }
  }

  removeOldestPos() {
    this.setState(prevState => ({
      on: [...prevState.on.slice(1)]
    }));
  }

  handleKeyDown(e) {
    if (e.keyCode === 37) {
      // this.setState(prevState => ({
      //   on: [...prevState.on, {posX: prevState.on[prevState.on.length-1].posX+1, posY: prevState.on[prevState.on.length-1].posY+1}],
      // }))
      this.addPos(1, 2);
    }
  }

  timeout() {
    setTimeout(function () {
      if (this.state.disabled === true) return;
      this.addPosFromDirection();
      this.removeOldestPos();
      this.timeout();
    }, 1000);
}

  render() {
    const items = [];
    for (let i = 0; i < 8; i++) {
      for (let e = 0; e < 8; e++) {
        items.push(<Square posX={e} posY={i} on={this.state.on} onClick={() => {
          this.setState( prevState => ({
            on: [...prevState.on, {posX: e, posY: i}],
        }))
        }}/>)
      }
    }
    this.timeout();
    return (
      <div className="game">
        <div className="game-board"  onKeyDown={this.handleKeyDown} tabIndex="0">
          {items}
        </div>
      </div>
    );
  }
}

ReactDOM.render(<Game />, document.getElementById("root"));