import React, { Component } from 'react'

export default class Square extends Component {
  render() {
    return (
    <div className={(this.props.on.map(e => JSON.stringify(e)).includes(JSON.stringify({
      posX: this.props.posX,
      posY: this.props.posY,
    }))) ? "dark" : "box" } onClick={this.props.onClick}>
    </div>
    ); 
  }
}
